import React from "react";

const TextSection = ({
  text,
  classNameText,
  title,
  classNameTitle,
  background = "rgba(0, 0, 0, 0.7) ",
  color = "white",
  className,
  Children
}) => {
  return (
    <div
      style={{
        textAlign: "center",
 background,
        color,
       
      }}
      className={`py-2 py-md-2 px-md-3 px-1 rounded ${className}`}
    >
      {title && <span className={`${classNameTitle} `}>{title} </span>}
      <span className={`${classNameText}`}>{text}</span>
      {Children&&Children}
      
    </div>
  );
};
export default TextSection;
