import React,{useState,useEffect,useRef} from 'react'

const Carousel=({items,delay,transition=0,className="w-100",font })=>{
  const [index, setIndex] = useState(0);
  const timeoutRef = useRef(null);

  const resetTimeout=()=>{
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
  }

  useEffect(() => {
    resetTimeout();
    timeoutRef.current = setTimeout(
      () =>
        setIndex((prevIndex) =>
          prevIndex === items.length - 1 ? 0 : prevIndex + 1
        ),
      delay
    );

    return () => {
      resetTimeout();
    };
  }, [delay, index, items.length]);

  return (
    <div className={className}  style={{  overflow: "hidden"}} >
      <div
        style={{ transform: `translate3d(${-index * 100}%, 0, 0)`,whiteSpace:"nowrap",transition:`ease ${transition}ms` }}
      >
        {items.map((item, i) => (
          <di  className="d-inline-block w-100 text-center py-3"><div
           
            style={{font}}
            key={i}
          >{item.title} </div>

       
        {item.image}
          </di>
                  ))}
      </div>
    </div>
  );
}
export default Carousel;